import React, { useEffect, useRef, useState } from 'react';

const Blank = (props) => {

    const [scrollY, setScrollY] = useState(0);
    function logit() {
        setScrollY(window.pageYOffset);

        // 
        let elementHeight = document.getElementsByClassName('navMenu')[0].scrollHeight +
            document.getElementsByClassName('navMenu2')[0].scrollHeight;
        let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
            - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
        // console.log(window.innerWidth);

        //    +  
        if (document.documentElement.scrollTop > elementHeight && window.innerWidth < 1024) {
            blockRef.current[0].style.display === 'inline-block' ?
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            blockRef.current[0].style.maxWidth = '420px';
            blockRef.current[1].style.left = '25%';

        }
        //   
        else if (document.documentElement.scrollTop > elementHeight && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
        //       + 
        else if (document.documentElement.scrollTop < elementHeight && window.innerWidth > 1023) {
            blockRef.current[0].style.position = 'relative';
            blockRef.current[0].style.left = '0px';
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[1].style.left = null;
        }
    }

    const blockRef = useRef([]);

    useEffect(() => {
        function watchScroll() {
            window.addEventListener("scroll", logit);
        }
        watchScroll();
        return () => {
            window.removeEventListener("scroll", logit);
        };
    });

    return (
        <>
            <button className="lblocknav_btn" onClick={() => {
                blockRef.current[0].style.display === 'inline-block' ?
                    blockRef.current[0].style.display = 'none' :
                    blockRef.current[0].style.display = 'inline-block';
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
            }}>  </button>
            <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
                <div className="lblocknav">
                    <span className="mtitle">
                        <a href="#" className="col_b"> <b> Intellij-Gradle-JPA-MySQL </b> </a>
                        <button className="lblocknav_btn2" onClick={() => {
                            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
                            document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
                        }}>  </button>
                    </span>
                    <p className="hyperlink">
                        <details open>
                            <summary> Spring   </summary>
                            <a href="#Eclipse Spring " className="col_r"> Eclipse Spring  </a>
                        </details>
                    </p>
                </div>
            </div>
            {/* <p> <img src={process.env.PUBLIC_URL + '/img/SpringExCode1/Directory_1.PNG'} alt='' /> </p> */}
            <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
                <span className="lblock">
                    <span className="mtitle"> <a name="">   </a> </span>
                    <p> <small>
                    </small> </p>
                    <span className="mblock">
                        <span className="stitle"> <a name="Eclipse Spring ">  Eclipse Spring  </a> </span>
                        <p> <i>   </i> </p>
                        <p> <i>1.</i> Window - Preferencs - enc  - Workspace, Css Files, HTML Files, JSP Files 
                            UTF-8     </p>
                        <p> <i>2.</i> Help - Eclipse MarketPlace - Spring Tools 3(Standalone Edition)  </p>
                        <p> <i>3.</i> Help - Eclipse MarketPlace - Spring Tools 3(Add-On for Spring Tools)) ?? </p>
                        <p> <i>4.</i> Help - Install New Software - Add - Name STS ,
                            Location https://dist.springsource.com/snapshot/TOOLS/nightly/e4.18 
                             Add 
                        </p>
                        <p> <i>5.</i>     Next       Next </p>
                        <p> <i>6.</i> File - New - Other - spring  - Spring Legacy Project -   -
                            templates Spring MVC Project    - com.spring.app   3 
                             
                        </p>
                        <p> <i>7.</i>   - Properties - Project Facets -  
                            (cmd java -version  ) -  runtimes   -   apply </p>
                        <p> <i>8.</i> src/main/resources  log4j.xml    </p>
                        <p> <i>9.</i> src - main - webapp - WEB-INF - spring views    web.xml &lt;web-app&gt; 
                                 </p>
                        <p> <i>10.</i> pom.xml springframework 4.2.4     </p>
                        <p> <i>11.</i>   - Maven - Update Project - </p>
                        <p> <i>12.</i>   Java Resources - Libraries - Maven Dependencies    </p>
                        <p> <i>13.</i>          
                            pom.xml java-version,  maven.plugins source,target  11() 
                              </p>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="Directory">  Directory </a> </span>
                    </span>
                </span>
            </div>
        </>
    );
}

export default Blank;